<template>
  <div class="table-content">
    <Form :model="StampItem" :label-width="80">
      <FormItem label="应税凭证：">
        <Input v-model="StampItem.taxable_name" placeholder="例：技术合同"></Input>
      </FormItem>
      <FormItem label="纳税期限：">
        <Select v-model="StampItem.taxes_status">
          <Option value="按月申报">按月申报</Option>
          <Option value="按次申报">按次申报</Option>
        </Select>
      </FormItem>
      <FormItem label="适用税率：">
        <Input v-model="StampItem.tax_rate" placeholder="例：0.00003"></Input>
      </FormItem>
      <Button type="success" ghost class="float-right" @click="addstamp">保存</Button>
    </Form>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    name: 'Stampadd',
    mixins: [common],
    data() {
      return {
        StampItem: {
          taxable_name: '',
          taxes_status: '',
          tax_rate: ''
        }
      }
    },
    methods: {
      addstamp() {
        this.$store.commit('savePreset', { name: 'YHS', data: this.StampItem })
        this.success()
        setTimeout(() => {
          layer.close(layer.index) //它获取的始终是最新弹出的某个层，值是由layer内部动态递增计算的
          Object.assign(this.$data, this.$options.data())
        }, 1500)
      }
    }
  }
</script>

<style scoped>
  .table-content {
    border-radius: 4px;
    padding: 10px;
    height: 100%;
    z-index: 0;
  }

  .stampSelect {
    width: 100%;
    height: 32px;
    box-sizing: border-box;
    vertical-align: middle;
    color: #515a6e;
    font-size: 14px;
    line-height: normal;
    border-radius: 4px;
    border: 1px solid #dcdee2;
    cursor: pointer;
  }
</style>



