<template>
  <div class="material">
    <div class="mb-5 company-top">
      <Button type="success" class="float-right" ghost>操作说明</Button>
      <Button type="success" class="float-right mr-10" ghost @click="toPreset">企业管理</Button>
    </div>
    <!--企业新增+业务新增-->
    <div class="company-info">
      <div class="row justify-content-between">
        <div class="float-left company-title">
          <strong class="company-name">企业名称：{{companyName}}</strong>
        </div>
        <div class="float-right text-center company-operating">
          <a class="mr-10" @click="addBusiness">新增业务</a>
          <a v-if="practicalAnswer" class="mr-10">答案修改</a>
          <a v-else class="mr-10" @click="toPracticalAnswer">答案绑定</a>
          <a class="mr-10">编辑</a>
          <a class="mr-10">删除</a>
        </div>
      </div>
    </div>
    <div class="table-responsive">
      <Table border :columns="tableTitle" :data="tableData" size="large" style="width: 100%;">
        <template slot-scope="{ row }" slot="content">
          <strong>{{ row.content }}</strong>
        </template>
        <template slot-scope="{ row, index }" slot="action">
          <a style="margin-right: 5px" @click="show(index)">查看</a>
          <a @click="remove(index)">删除</a>
        </template>
      </Table>
    </div>
    <!--分页部分-->
    <div class="mt-15 text-center">
      <Page :total="dataCount" size="small" :page-size="pageSize" class="paging" @on-change="changepage"></Page>
    </div>
    <!--业务新增弹窗内容-->
    <div v-show="showPreseteadd">
      <div class="showPreseteadd">
        <Preseteadd></Preseteadd>
      </div>
    </div>
    <!--企业新增模拟弹窗内容-->
    <div v-show="showCompanyadd">
      <Popups>
        <div slot="title">企业数据设置</div>
        <div slot="close">
          <Icon type="md-close companyClose" @click="showCompanyaddoff"/>
        </div>
        <div slot="content">
          <div class="preset">
            <Tabs type="card">
              <TabPane label="企业基本信息设置">
                <!--企业基本信息设置-->
                <div class="table-content">
                  <form class="companyInfo">
                    <Table border size="small" :columns="companyTitle" :data="companyData"></Table>
                  </form>
                  <Button type="success" class="mt-10 float-right" ghost @click="saveCompanyInfo">保 存</Button>
                </div>
              </TabPane>
              <TabPane label="印花税申报表">
                <!--印花税预设数据-->
                <div class="table-content">
                  <div class="d-flex">
                    <Button type="success" class="float-right" style="margin-bottom: 10px;" ghost @click="addStampDuty">
                      新增数据 +
                    </Button>
                    <Divider/>
                  </div>
                  <div class="business-list" style="height: 80%; overflow: auto;">
                    <Row>
                      <Col span="6" v-for="item,index in stampList" :key="index" class="mr-5 mt-5"
                           style="min-width: 200px;">
                        <Card>
                          <p slot="title">数据 {{index}}</p>
                          <p>应税凭证名称：{{item.taxable_name}}</p>
                          <p>纳税期限：{{item.taxes_status}}</p>
                          <p>适用税率：{{item.tax_rate}}</p>
                          <p>
                            <a class="mr-3">删除</a>
                            <a>修改</a>
                          </p>
                        </Card>
                      </Col>
                    </Row>
                  </div>
                </div>
              </TabPane>
              <TabPane label="增值税申报表">
                <!--增值税信息设置-->
                <div class="table-content">
                  <div class="vat-button" title="增值税纳税申报表附列资料（一）">
                    <p>增值税纳税申报表附列资料（一）</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="增值税纳税申报表附列材料（二）">
                    <p>增值税纳税申报表附列材料（二）</p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="增值税纳税申报表附列材料（三）">
                    <p>增值税纳税申报表附列材料（三）</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="增值税纳税申报表附列材料（四）">
                    <p>增值税纳税申报表附列材料（四）</p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="增值税纳税申报表附列材料（五）">
                    <p>增值税纳税申报表附列材料（五）</p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="增值税纳税申报表（适用于增值税一般纳税人）">
                    <p>增值税纳税申报表（适用于增值税一般纳税人）</p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="增值税补充申报表">
                    <p>增值税补充申报表</p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="增值税减免税申报明细表">
                    <p>增值税减免税申报明细表</p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="城建税、教育费附加、地方教育附加税（费）申报">
                    <p>城建税、教育费附加、地方教育附加税（费）申报</p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="增值税减免税申报明细表">
                    <p>增值税减免税申报明细表</p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                </div>
              </TabPane>
              <TabPane label="财务报表">
                <!--财务报表信息设置-->
                <div class="table-content">
                  <Button title="资产负债表" @click="openTable('p7')">
                    资产负债表
                    <Icon v-if="tableStatus.p7" type="ios-star"/>
                    <Icon v-else type="ios-star-outline"/>
                  </Button>
                  <Button title="利润表">
                    利润表
                    <Icon type="ios-star-outline"/>
                    <Icon type="ios-star"/>
                  </Button>
                </div>
              </TabPane>
              <TabPane label="企业所得税申报表【季报】">
                <!--所得税季报-->
                <div class="table-content">
                  <div class="vat-button" title="不征税收入和税基类减免应纳税所得额明细表">
                    <p>不征税收入和税基类减免应纳税所得额明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="固定资产加速折旧(扣除)明细表">
                    <p>固定资产加速折旧(扣除)明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="企业所得税季度预缴纳税申报表">
                    <p>企业所得税季度预缴纳税申报表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="减免所得税额明细表">
                    <p>减免所得税额明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                </div>
              </TabPane>
              <TabPane label="企业所得税申报表【年报】">
                <!--所得税年报-->
                <div class="table-content">
                  <div class="vat-button" title="中华人民共和国企业所得税年度纳税申报表（A类）">
                    <p>中华人民共和国企业所得税年度纳税申报表（A类）</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="一般企业收入明细表">
                    <p>一般企业收入明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="一般企业成本支出明细表">
                    <p>一般企业成本支出明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="期间费用明细表">
                    <p>期间费用明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="纳税调整项目明细表">
                    <p>纳税调整项目明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="职工薪酬支出及纳税调整明细表">
                    <p>职工薪酬支出及纳税调整明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="资产折旧、摊销及纳税调整明细表">
                    <p>资产折旧、摊销及纳税调整明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="广告费和业务宣传费跨年度纳税调整明细表">
                    <p>广告费和业务宣传费跨年度纳税调整明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="减免所得税优惠明细表">
                    <p>减免所得税优惠明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="企业所得税弥补亏损明细表">
                    <p>企业所得税弥补亏损明细表</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                </div>
              </TabPane>
            </Tabs>
          </div>
        </div>
      </Popups>
    </div>
    <!--印花税新增-->
    <div v-show="showStamp">
      <div class="showStamp">
        <Stamp></Stamp>
      </div>
    </div>
    <!--预设表部分-->
    <div v-show="showTablePreset">
      <div v-if="tableCode == 'p7'" class="p7">
        <P7></P7>
      </div>
    </div>
  </div>
</template>

<script>
  // 业务新增
  import Preseteadd from './preset/presetadd'
  import common from '../mixins/common'
  import { Quill } from 'vue-quill-editor'
  import { ImageExtend, QuillWatch } from 'quill-image-extend-module'
  import Popups from './pop-ups-model' // 模拟弹窗组件
  import Stamp from './preset/stampadd' // 印花税数据新增
  // 预设表格引入
  import P7 from './preset/preset007'

  Quill.register('modules/ImageExtend', ImageExtend)
  export default {
    name: 'Material',
    mixins: [common],
    components: {
      Preseteadd,
      Popups,
      Stamp,
      P7 // 资产负债表
    },
    data() {
      return {
        // 状态依据
        showPreseteadd: false,
        showCompanyadd: false,
        showTablePreset: false,
        // 答案存在依据
        practicalAnswer: false,
        // 表格数据区
        tableTitle: [
          {
            title: '业务内容',
            key: 'content'
          },
          {
            title: '分值',
            key: 'course',
            width: 100,
            align: 'center'
          },
          {
            title: '操作',
            slot: 'action',
            width: 150,
            align: 'center'
          }
        ],
        tableData: [], // 业务数据集合
        // 分页数据
        ajaxHistoryData: [],
        // 初始化信息总条数
        dataCount: 0,
        // 每页显示多少条
        pageSize: 1,
        // 当前页码
        page: 1,
        historyData: [], // 当前企业键值
        companyName: '', // 企业名称
        // 富文本配置
        editorOption: {
          placeholder: '',
          modules: {
            ImageExtend: {
              loading: true,
              name: 'file',
              size: 1, // 单位为M, 1M = 1024KB
              // action: window.host + 'uploadEditorImg',
              action: '',
              response: (res) => {
                if (res.status === 1) {
                  return res.resultData
                } else {
                  this.$message.error(res.info)
                }
              },
              sizeError: () => {
                this.$message.error('图片尺寸最大为1M')
              },
              // 可选参数 设置请求头部
              headers: (xhr) => {
                xhr.setRequestHeader('token', this.$store.state.access_token_teacher)
              }
            },
            toolbar: {
              container: ['bold', 'italic', 'underline', { 'script': 'sub' }, { 'script': 'super' }, 'clean', 'image'],
              handlers: {
                'image': function () {
                  QuillWatch.emit(this.quill.id)
                }
              }
            }
          }
        },
        // 基本信息设置
        // 印花税新增
        showStamp: false,
        stampList: [],
        //企业信息表
        companyTitle: [
          {
            title: '项目',
            key: 'projectA',
            width: 115
          },
          {
            title: '内容',
            key: 'contentA',
            render: (h, params) => {
              return h('div', [
                h('Input', {
                  props: {
                    placeholder: this.companyPrompt[params.index][params.column._index].prompt,
                    name: this.companyPrompt[params.index][params.column._index].name,
                    value: this.companyPrompt[params.index][params.column._index].value
                  },
                  title: this.companyPrompt[params.index][params.column._index].value,
                  style: {
                    marginRight: '5px'
                  }
                }, 'View')
              ])
            }
          },
          {
            title: '项目',
            key: 'projectB',
            width: 115
          },
          {
            title: '内容',
            key: 'contentB',
            render: (h, params) => {
              return h('div', [
                h('Input', {
                  props: {
                    placeholder: this.companyPrompt[params.index][params.column._index].prompt,
                    name: this.companyPrompt[params.index][params.column._index].name,
                    value: this.companyPrompt[params.index][params.column._index].value
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.abc(params)
                    }
                  }
                }, 'View')
              ])
            }
          }
        ],
        companyData: [
          {
            projectA: '企业编码：',
            projectB: '纳税人名称：'
          },
          {
            projectA: '纳税人识别号：',
            projectB: '所属行业：'
          },
          {
            projectA: '明细行业：',
            projectB: '登记行业：'
          },
          {
            projectA: '法人姓名：',
            projectB: '注册地址：'
          },
          {
            projectA: '经营地址：',
            projectB: '开户行账号：'
          },
          {
            projectA: '注册类型：',
            projectB: '联系电话：'
          },
          {
            projectA: '财务负责人姓名：',
            projectB: '办税人员姓名：'
          }
        ],
        // 表单提示信息
        companyPrompt: {
          0: {
            1: {
              prompt: '例：12 (非0、整数、纯数字、无重复)',
              name: 'trade_id',
              value: ''
            },
            3: {
              prompt: '例：山东问云机械有限公司',
              name: 'firm_name',
              value: '山东问云机械有限公司'
            }
          },
          1: {
            1: {
              prompt: '例：91370305755666003U',
              name: 'firm_code',
              value: '91370305755666003U'
            },
            3: {
              prompt: '例：其他软件开发',
              name: 'firm_trade',
              value: '其他软件开发'
            }
          },
          2: {
            1: {
              prompt: '例：其他软件开发',
              name: 'firm_detail_trade',
              value: '其他软件开发'
            },
            3: {
              prompt: '例：其他有限责任公司',
              name: 'firm_registration_trade',
              value: '其他有限责任公司'
            }
          },
          3: {
            1: {
              prompt: '例：王小二',
              name: 'firm_legal_person',
              value: '王小二'
            },
            3: {
              prompt: '例：山东省淄博市张店区东方之珠',
              name: 'firm_address',
              value: '山东省淄博市张店区东方之珠'
            }
          },
          4: {
            1: {
              prompt: '例：山东省淄博市张店区东方之珠',
              name: 'firm_operating',
              value: '山东省淄博市张店区东方之珠'
            },
            3: {
              prompt: '例：5159889499889556',
              name: 'firm_bank_code',
              value: '5159889499889556'
            }
          },
          5: {
            1: {
              prompt: '例：其他有限责任公司',
              name: 'firm_type',
              value: '其他有限责任公司'
            },
            3: {
              prompt: '例：15275918888',
              name: 'firm_telephone',
              value: '15275918888'
            }
          },
          6: {
            1: {
              prompt: '例：李春华',
              name: 'telephone_name',
              value: '李春华'
            },
            3: {
              prompt: '例：苏三',
              name: 'taxation_name',
              value: '苏三'
            }
          }
        },
        // 表数据预设状态
        tableStatus: {
          p7: false
        },
        tableCode: '' //加载哪张表的依据
      }
    },
    created() {
      this.handleListApproveHistory()
    },
    computed: {
      editor() {
        return this.$refs.myQuillEditor.quill
      }
    },
    methods: {
      // 企业基本信息设置
      toPreset() {
        this.showCompanyadd = true
      },
      showCompanyaddoff() {
        this.showCompanyadd = false
      },
      // 实操系统答案绑定
      toPracticalAnswer() {
        this.practicalAnswer = true
      },
      // 业务新增
      addBusiness() {
        this.showPreseteadd = true
        layui.use('layer', () => {
          var layer = layui.layer
          setTimeout(() => {
            var index = layer.open({
              type: 1,
              title: '业务数据设置',
              shadeClose: false,
              shade: 0.2,
              area: ['500px', '510px'],
              content: J('.businessAdd'), //iframe的url
              end: () => {
                this.showPreseteadd = false
              }
            })
          }, 5)
        })
      },
      // 业务信息保存
      saveBusinessInfo() {
        const trade_id = this.$store.state.preset.trade_id
        var preset = this.preset
        this.$store.commit('saveBusiness', { name: trade_id, data: preset })
        if (this.$store.state.preset.companyInfo.business[trade_id]) {
          this.success()
          layer.close(layer.index) //再执行关闭
        }
      },
      show(index) {
        this.$Modal.info({
          title: '业务详情',
          content: `业务内容：${this.tableData[index].content}<br>业务分值：${this.tableData[index].course}<br>文字解析：${this.tableData[index].textAnalysis}<br>视频解析：${this.tableData[index].videoAnalysis}`
        })
      },
      remove(index) {
        this.tableData.splice(index, 1)
      },
      // 分页控制
      // 获取历史记录信息
      handleListApproveHistory() {
        // 保存取到的所有数据
        this.ajaxHistoryData = Object.keys(this.$store.state.preset.companyInfo.company)

        this.dataCount = Object.keys(this.$store.state.preset.companyInfo.company).length
        // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
        if (Object.keys(this.$store.state.preset.companyInfo.company).length < this.pageSize) {
          this.historyData = this.ajaxHistoryData
        } else {
          const stateCompany = this.$store.state.preset.companyInfo.company
          this.historyData = this.ajaxHistoryData.slice(0, this.pageSize)
          // 单独拿出当前企业的名称信息
          this.companyName = stateCompany[this.historyData][1].value
          //保存所操作企业的键值
          this.$store.commit('saveTradeid', this.historyData)
          // 同步业务信息
          this.tableDataInfo()
          //同步印花税数据信息
          this.stampList = this.$store.state.preset.companyInfo.preset[this.historyData]['YHS']
        }
      },
      changepage(index) {
        // 当前页码
        this.page = index
        let _start = (index - 1) * this.pageSize
        let _end = index * this.pageSize
        this.historyData = this.ajaxHistoryData.slice(_start, _end)
        // 单独拿出当前企业的名称信息
        const stateCompany = this.$store.state.preset.companyInfo.company
        this.companyName = stateCompany[this.historyData][1].value
        // 保存所操作企业的键值
        this.$store.commit('saveTradeid', this.historyData)
        this.tableDataInfo()
      },
      tableDataInfo() {
        this.tableData = this.$store.state.preset.companyInfo.business[this.historyData]
      },
      // 印花税业务新增
      addStampDuty() {
        this.showStamp = true
        layui.use('layer', () => {
          var layer = layui.layer
          setTimeout(() => {
            var index = layer.open({
              type: 1,
              title: '印花税数据新增',
              shadeClose: false,
              shade: 0,
              area: ['300px', '290px'],
              content: J('.showStamp'),
              end: () => {
                this.showStamp = false
                this.stampList = this.$store.state.preset.companyInfo.preset[this.historyData]['YHS']
              }
            })
          }, 5)
        })
      },
      // 预设表数据
      openTable(tableCode) {
        this.showTablePreset = true
        this.tableCode = tableCode
        layui.use('layer', () => {
          var layer = layui.layer
          setTimeout(() => {
            var index = layer.open({
              type: 1,
              title: '表预设数据设置',
              shadeClose: false,
              shade: 0,
              area: ['95%', '95%'],
              content: J('.' + tableCode),
              end: () => {
                this.showTablePreset = false
              }
            })
          }, 5)
        })
      }
    }
  }
</script>

<style scoped>
  /*业务弹窗*/
  .businessAdd {
    width: 100%;
    overflow: auto;
    padding: 10px;
  }

  .material {
    width: 100%;
    height: 100%;
    padding: 20px;
    overflow: auto;
  }

  .company-top {
    height: 35px;
  }

  .company-name {
    font-size: 14px;
    color: #515a6e;
  }

  .company-info {
    height: 40px;
    line-height: 30px;
    border: 1px solid #dee2e6;
    border-bottom: 0px;
  }

  .company-info .justify-content-between {
    padding-left: 15px;
    height: 100%;
  }

  .company-info .company-title {
    line-height: 40px;
    margin-left: 3px;
  }

  .company-info .company-operating {
    width: 250px;
    line-height: 40px;
    float: right;
  }

  a {
    color: #40ADDC !important;
    font-size: 13px;
    font-weight: 400;
  }

  .company-top button {
    color: #7CCB40;
    border-color: #7CCB40;
    width: 86px;
    height: 30px;
  }

  .company-top button:hover,
  .company-top button:active {
    color: #7CCB40 !important;
    background-color: #ffffff !important;
    border-color: #7CCB40 !important;
  }

  .companyClose {
    font-size: 20px;
    color: #65646E;
  }

  .companyClose:hover {
    color: #919098;
  }

  /*企业预设信息样式*/
  .preset {
    width: 100%;
    height: 100%;
    padding: 20px;
  }

  .preset .table th {
    vertical-align: inherit !important;
  }

  .preset .table-content {
    height: 370px;
  }


</style>



