<template>
  <div class="businessAdd">
    <Form :model="preset" :label-width="80" style="padding-right: 15px;">
      <FormItem label="业务内容">
        <Input v-model="preset.content" type="textarea" placeholder="业务描述"/>
      </FormItem>
      <FormItem label="分值">
        <Input v-model="preset.course" placeholder="例: 10%"></Input>
      </FormItem>
      <FormItem label="文字解析">
        <!--富文本-->
        <quill-editor
          id="textAnalysis"
          v-model="preset.textAnalysis"
          ref="myQuillEditor"
          :options="editorOption">
        </quill-editor>
      </FormItem>
      <FormItem label="视频解析">
        <Upload accept="mp4" action="//jsonplaceholder.typicode.com/posts/">
          <Button icon="ios-cloud-upload-outline">选择视频文件</Button>
        </Upload>
      </FormItem>
      <FormItem label="材料上传">
        <Upload action="//jsonplaceholder.typicode.com/posts/">
          <Button icon="ios-cloud-upload-outline">选择相关材料</Button>
        </Upload>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="saveBusinessInfo">保存</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  import common from '../../mixins/common'
  import { Quill } from 'vue-quill-editor'
  import { ImageExtend, QuillWatch } from 'quill-image-extend-module'

  Quill.register('modules/ImageExtend', ImageExtend)
  export default {
    name: 'Presetadd',
    mixins: [common],
    data() {
      return {
        preset: {
          content: '', // 业务内容
          course: '', // 分值
          textAnalysis: '', // 文字解析
          videoAnalysis: [
            {
              name: 'food.jpeg',
              url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
            },
            {
              name: 'food2.jpeg',
              url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
            }
          ], // 视频解析
          material: [
            {
              name: 'food.jpeg',
              url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
            },
            {
              name: 'food2.jpeg',
              url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
            }
          ] //材料
        },
        // 富文本配置
        editorOption: {
          placeholder: '',
          modules: {
            ImageExtend: {
              loading: true,
              name: 'file',
              size: 1, // 单位为M, 1M = 1024KB
              // action: window.host + 'uploadEditorImg',
              action: '',
              response: (res) => {
                if (res.status === 1) {
                  return res.resultData
                } else {
                  this.$message.error(res.info)
                }
              },
              sizeError: () => {
                this.$message.error('图片尺寸最大为1M')
              },
              // 可选参数 设置请求头部
              headers: (xhr) => {
                xhr.setRequestHeader('token', this.$store.state.access_token_teacher)
              }
            },
            toolbar: {
              container: ['bold', 'italic', 'underline', { 'script': 'sub' }, { 'script': 'super' }, 'clean', 'image'],
              handlers: {
                'image': function () {
                  QuillWatch.emit(this.quill.id)
                }
              }
            }
          }
        }
      }
    },
    computed: {
      editor() {
        return this.$refs.myQuillEditor.quill
      }
    },
    methods: {
      // 业务信息保存
      saveBusinessInfo() {
        const trade_id = this.$store.state.preset.trade_id
        var preset = this.preset

        this.$store.commit('saveBusiness', { name: trade_id, data: preset })
        if (this.$store.state.preset.companyInfo.business[trade_id]) {
          this.success()
          layer.close(layer.index) //再执行关闭
          // data数据初始化
          Object.assign(this.$data, this.$options.data())
        }
      }
    }
  }
</script>

<style scoped>
  /*业务弹窗*/
  .businessAdd {
    width: 100%;
    overflow: auto;
    padding: 10px;
  }

  .businessAdd .row {
    margin: 0px auto;
  }
</style>



